@import "../../variables.scss";

.landing-page {
  background-image: url("../../static/img/room_landing.png");
  background-size: cover;
  background-repeat: no-repeat;

  height: 830px;

  &__center {
    display: block;
    width: 100%;
    max-width: 1200px;
    height: 100%;
    position: relative;

    padding-left: 20px;
    padding-right: 20px;

    margin: 0 auto;
  }

  &__form {
    display: block;
    position: relative;
    top: 71px;
  }

  &__message {
    display: block;
    max-width: 300px;
    position: absolute;

    color: $fontColorLight75;

    text-align: right;
    line-height: 23px;

    right: 15px;
    bottom: 8px;
  }
}

@media (max-width: 600px) {
  .landing-page__form {
    display: flex;
    justify-content: center;
  }
}
