@import "../../variables.scss";

.footer {
  display: block;
  width: 100%;

  color: $fontColorLight75;

  font-size: 14px;
  line-height: 24px;

  &__center {
    max-width: 1200px;

    margin: 0 auto;
    padding: 0 21px 0 20px;
  }

  &__top {
    padding-top: 100px;
    padding-bottom: 83px;
    border-bottom: 1px solid $fontColorLight10;
    border-top: 1px solid $fontColorLight10;
  }

  &__top-inner {
    display: flex;
    justify-content: space-between;
  }

  &__top-left,
  &__top-right {
    width: 260px;
  }

  &__top-left-message {
    margin-top: 10px;
  }

  &__top-right-title {
    display: block;

    margin: 0;

    font-size: 12px;
    text-transform: uppercase;
  }

  &__top-right-message {
    display: block;

    margin-top: 12.5px;
    margin-bottom: 23px;
  }

  &__bottom {
    line-height: 18px;
  }

  &__bottom-inner {
    display: flex;
    justify-content: space-between;

    padding-top: 26px;
    padding-bottom: 26px;
  }

  &__bottom-icons {
    display: flex;

    margin-top: -2px;
    margin-right: 2px;
  }

  &__bottom-icon {
    margin-left: 22px;
  }

  &__bottom-copyright {
    margin-left: -1px;
  }

  &__bottom-icon-link {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
  }

  &__bottom-icon:first-child {
    margin-right: -1px;
  }
}

@media (max-width: 1200px) {
  .footer__wrapper {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media (max-width: 992px) {
  .footer__top-right {
    display: none;
  }

  .footer__top-inner {
    justify-content: center;

    text-align: center;
  }

  .footer__top-left {
    width: 100%;
    max-width: 560px;
  }

  .footer__top {
    border-bottom: none;
    padding-bottom: 0;
  }

  .footer__bottom-copyright {
    display: none;
  }

  .footer__bottom-inner {
    justify-content: center;

    padding-top: 10px;
  }
}
