.footer-bottom {
  line-height: 18px;

  &__center {
    max-width: 1200px;

    margin: 0 auto;
    padding: 0 21px 0 20px;
  }

  &__inner {
    display: flex;
    justify-content: space-between;

    padding-top: 26px;
    padding-bottom: 26px;
  }

  &__icons {
    display: flex;

    margin-top: -2px;
    margin-right: 2px;
  }

  &__icon {
    margin-left: 22px;
  }

  &__copyright {
    margin-left: -1px;
  }

  &__icon-link {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: center;
  }

  &__icon:first-child {
    margin-right: -1px;
  }
}
